<template>
  <div class="chart-item">
    <ve-bar :data="chartData" :settings="chartSettings" :extend="chartExtend" height="400px" :grid="grid"></ve-bar>
    <div v-show="chart.length === 0" class="chart-no-data">No data</div>
  </div>
</template>

<script>
module.exports = {
  data() {
    return {
      chartExtend: {},
      grid: {},
      chartSettings: {}
    }
  },
  props: {
    year: {
      default: ''
    },
    chart: {
      type: Array
    }
  },
  created() {
    const fadeRows = [
      { major: '', score: 120, userScore: 89 },
      { major: '', score: 110, userScore: 89 },
      { major: '', score: 98, userScore: 89 },
      { major: '', score: 96, userScore: 89 },
      { major: '', score: 90, userScore: 89 },
      { major: '', score: 88, userScore: 89 },
      { major: '', score: 78, userScore: 89 },
      { major: '', score: 65, userScore: 89 },
      { major: '', score: 59, userScore: 89 },
      { major: '', score: 50, userScore: 89 }
    ].reverse()

    this.chartData = {
      columns: ['major', 'score', 'userScore'],
      rows: this.chart
    }

    let maxArray = []
    const array = this.chart.forEach(item => {
      maxArray.push(Number(item.score))
      maxArray.push(Number(item.userScore))
    })

    this.chartSettings = {
      label: {
        normal: { show: true, position: 'right', formatter: '{c}' }
      },
      max: [this.$_.max(maxArray) + ''],
      legendName: {
        score: `${this.year}`,
        userScore: ``
      }
    }

    this.chartExtend = {
      series(items) {
        items.forEach(item => {
          if (item.name === 'userScore') {
            item.type = 'line'
            item.label = {
              show: false,
              position: 'right',
              formatter: '{c}'
            }
          } else {
            item.label = {
              show: true,
              formatter: '{c}'
            }
          }
        })

        return items
      }
    }

    this.grid = [
      {
        top: '50px',
        right: '30px'
      }
    ]
  }
}
</script>


