<template>
  <div class="answer">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>

    <van-row :gutter="20" class="info">
      <van-col span="24" class="info-item"> {{data.info.university}}</van-col>
      <van-col span="24" class="info-item"> {{data.info.name}}</van-col>
      <van-col span="24" class="info-item">{{data.info.cardNo}}</van-col>
      <van-col span="24" class="info-item"> {{data.info.score}}</van-col>
      <van-col span="24" class="info-item"> {{data.info.rate}}</van-col>
      <van-col span="24" class="info-item">
        <span class="item-red" v-if="data.info.popular">{{data.info.popular.split(',')[0]}}</span> /
        <span class="item-blue" v-if="data.info.popular">{{data.info.popular.split(',')[1]}}</span>
      </van-col>
      <van-col span="24" class="info-item item-tip"></van-col>
      <van-col span="24" class="info-item"> {{level}}</van-col>
      <van-col span="24" class="info-item"> {{data.info.province}}</van-col>
      <van-col span="24" class="info-item"> {{data.info.phone}}</van-col>
      <van-col span="24" class="info-item">
        <span class="item-blue">{{data.info.website}}</span>
      </van-col>
    </van-row>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
         
      </van-col>
    </van-row>
    <wap-history class="chart-position" :chart="data.chart1" v-if="data.chart1.length !== 0"></wap-history>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
        
      </van-col>
    </van-row>
    <wap-chart-underscore class="chart-position" :year="new Date().getFullYear() - 3 " :chart="data.chart2" v-if="data.chart2.length !== 0"></wap-chart-underscore>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
        
      </van-col>
      <van-col span="24" class="chart-tips">
         2015 2016 2017
      </van-col>
    </van-row>
    <wap-chart-course-percent class="chart-position" v-if="GotData" :chart="data.chart3"></wap-chart-course-percent>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 3 " v-if="GotData" :chart="data.chart4"></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 2 " v-if="GotData" :chart="data.chart5"></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 1" v-if="GotData" :chart="data.chart6"></wap-chart-course-score>

    <van-row :gutter="20">
      <van-col span="24" class="tip-red">
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      levelMap: [
        {
          id: '1',
          label: '211'
        },
        {
          id: '2',
          label: '985'
        },
        {
          id: '3',
          label: '211 + 985'
        },
        {
          id: '4',
          label: ''
        }
      ],
      level: '',
      data: {
        info: {},
        chart1: [],
        chart2: [],
        chart3: [],
        chart4: [],
        chart5: [],
        chart6: []
      },
      GotData: false
    }
  },
  methods: {
    fetchData() {
      const vm = this
      const param = {
        wx_id: this.$store.state.userinfo.openid,
        id: this.$route.query.id
      }
      this.$store.commit(this.$types.ShowLoading, true)
      this.$api.GetTestAnswerById(param).then(res => {
        if (res.data.code == '200') {
          vm.data = res.data.data
          vm.GotData = true
        }
        vm.$store.commit(vm.$types.ShowLoading, false)
      })
    }
  },
  created() {
    this.fetchData()
  },
  watch: {
    'data.info.level': {
      handler(val) {
        const item = this.$_.find(this.levelMap, { id: val })
        this.level = item ? item.label : '-'
      }
    }
  }
}
</script>


<style lang="scss" scoped>
.info {
  font-size: 13px;
  padding-top: 10px;
  .info-item {
    padding-top: 5px;
    .item-red {
      color: #cc0000;
    }

    .item-blue {
      color: #0066ff;
    }

    &.item-tip {
      color: #cc0000;
      font-size: 12px;
    }
  }

  .rate {
    display: inline-block;
    position: relative;
  }
}

.chart-item {
  margin-top: 20px;
}

.chart-position {
  bottom: -20px;
}

.tip-red {
  color: #cc0000;
  font-size: 12px;
}

.chart-tips {
  color: #cc0000;
  font-size: 12px;
  padding: 10px 5px;
}
</style>



